.wrapper{
    width: 95%;
    overflow: hidden;
    border: 2px solid grey;
    border-radius: 10px;
    margin: auto;
}

.H_block{
    width: 60%;
    overflow: hidden;
    margin: 10px 10px;
    border: 3px solid grey;
    border-radius: 10px;
    float: left;
}
.Rezultat{
    width: 400px;
    overflow: hidden;
    border: 3px solid grey;
    border-radius: 10px;
    float: right;
    margin: 10px 40px;
}
form{
    width: 80%;
    overflow: hidden;
    margin: 20px 10%;
    border: 3px solid grey;
    border-radius: 5px;
    float: left;
}
.twoForm{
    width: 100%;
    margin: 0px auto;
    overflow: hidden;
    float: left;
}
.ContForm{
    width: 90%;
    overflow: hidden;
    margin: 10px 5%;
    display: block;
}
.InBlock{
    width: 50%;
    overflow: hidden;
}
label{
    font-size: 1.5em;
    float: left;
}
input,select,textarea{
    font-size: 0.8em;
    font-weight: bolder;
    height: 40px;
    margin-top: 5px;
    margin-bottom: 5px;
}
.H_contact{
    background-color: rgb(21, 181, 230);
    margin: 0;
    padding: 10px 0;
    margin-bottom: 10px;
    color: white;
}
.Cont_In_box1{
    width: 50%;
    overflow: hidden;
    float: left;
}
.Cont_In_box1 > .InBlock{
    width: 100%;
    overflow: hidden;
}
.Cont_l1{
    font-weight: bolder;
}
.Cont_in1{
    width: 90%;
    margin-left: 20px;
}
.Sel{
    width: 90%;
    margin: 20px 0px 20px 30px;
    float: left;
    font-size: 1.2em;
}
.InBlock2{
    width: 50%;
    overflow: hidden;
    float: right;
}
.Cont_l2{
    width: 100%;
    font-weight: bolder;
    
}
.TextAr2{
    width: 80%;
    height: 135px;
    margin-bottom: 20px;
    font-size: 2em;
    font-weight: bolder;
}
.ButtonCont1{
    width: 150px;
    height: 60px;
    background-color: rgb(231, 77, 31);
    color: white;
    font-size: 1.2em;
    font-weight: bolder;
    float: left;
    margin: 20px 5px 20px 30px;
}
.ButtonCont1:hover{
    background-color: rgba(231, 77, 31, 0.8);
}
.ButtonCont2{
    width: 150px;
    height: 60px;
    margin-left: 10px;
    color: rgb(141, 139, 139);
    font-size: 1.0em;
    font-weight: bolder;
    float: left;
    margin: 20px 10px;
}
.RezP{
    font-size: 1em;
    font-weight: bolder;
    text-align: left;
    margin-left: 10px;
}
.Line{
    width: 90%;
    height: 5px;
    background-color: grey;
    margin: auto;
}